.ButtonPanel {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;  /* Дозволяє елементам переходити на новий рядок при необхідності */
    gap: 10px; /* Відстань між картками */
}

/* Медіа-запит для екрану до 768px (наприклад, для планшетів або мобільних пристроїв) */
@media (max-width: 768px) {
    .ButtonPanel {
        justify-content: flex-start; /* Вирівнює картки до лівого краю */
        gap: 5px; /* Менша відстань між картками на малих екранах */
    }
    .TaskImg {
      max-width: 300px;
    }
}

/* Медіа-запит для екрану до 480px (наприклад, для мобільних телефонів) */
@media (max-width: 480px) {
    .ButtonPanel {
        justify-content: center; /* Вирівнює картки по центру */
        gap: 3px; /* Ще менша відстань між картками */
    }
    .TaskImg {
      max-width: 200px;
    }
}
.panel {
    background-color: #2d2c3193;
    border: 1px solid #4d4d4d;
    border-radius: 5px;
    padding: 10px;
    text-decoration: none;
    margin: 1%;
    backdrop-filter: blur(5px);
    color: #ffffff;
    text-shadow: #000000 2px 2px 1px;
    box-shadow: #6e6e6e5b 5px 5px 6px;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    gap: 20px;
}

.dropdown-panel {
    border: 1px solid #414141;
    padding: 10px;
    text-decoration: none;
    margin: 1%;
    backdrop-filter: blur(5px);
    color: #ffffff;
    text-shadow: #000000 2px 2px 1px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    gap: 20px;
}

/* Header.css */
.Header {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 0 10px 0 0;
    background-color: #222;
    color: white;
    flex-direction: row-reverse;
    position: fixed;
    z-index: 10;
    width: 100%;
  }

  .GlobalButtonContainer{
    padding: 60px 20px 0;
  }
  
  .logo {
    font-size: 16px;
    font-weight: bold;
    text-decoration: none;
    color: white;
  }

  .logo a {
    font-size: 24px;
    font-weight: bold;
    text-decoration: none;
    color: white;
  }
  
  /* DropdownMenu.css */
.dropdown {
    position: relative;
  }
  
  .dropdown-button {
    padding: 10px 20px; /* Збільшення відступів для кнопки */
    font-size: 26px; /* Встановлення розміру шрифту */
    background-color: #222222; /* Колір фону */
    color: white; /* Колір тексту */
    border: none; /* Без бордера */
    border-radius: 4px; /* Закруглення кутів */
}

    .close-dropdown-button {
    top: 10px;
    right: 10px;
    background-color: #333;
    color: white;
    border: none;
    border-radius: 4px;
    padding: 10px 20px;
    font-size: 24px;
    cursor: pointer;
    }
  
  .dropdown-menu {
    position: fixed;
    top: 0; /* Випадаюче меню буде зафіксовано в верхній частині екрану */
    left: 0;
    width: 250px;
    height: 100vh;
    background-color: #333;
    color: white;
    box-shadow: 2px 0px 5px rgba(0, 0, 0, 0.5);
    transform: translateX(-100%);
    transition: transform 0.3s ease;
    z-index: 1000; /* Робимо меню поверх всіх елементів */
  }  
  
  .dropdown-menu.open {
    transform: translateX(0); /* При відкритті меню, воно виїжджає */
  }
  
  .preview {
    min-width: 200px;
    min-height: 40px;
    margin: 0;
    padding: 0;
  }
  
  .Button a {
    color: white;
    text-decoration: none;
  }
  
  .Button a:hover {
    background-color: #555;
  }
  
  .Button {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
  }

.btn{
    text-decoration: none;
    align-items: center;
}
.btn-primary {
    border-color: #00000000;
    border-radius: 0 0 3px 3px !important;
    text-decoration: none;
}
.btn-secondary{
    width: 100%;
    height: 100%;
    display: flex;
    border-radius: 5% !important;
    justify-content: center;
}
.card {
    height: 10%;
    display: flex;
    align-items: stretch;
}
.container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 2fr)); /* Адаптивна сітка з рядками, які мають ширину 200px */
    gap: 20px; /* Відстань між елементами */
    padding: 10px;
    width: 100%;
}