@media (max-width: 710px) {
    .panel {
        min-width: 50px;
        max-width: 100%;
        margin: 20px 0 20px 0;
        overflow: hidden;
    }
    .back {
        top: 0px;
        z-index: 100;
    }
    p, h1, h2, h3, h4, h5, h6 {
        font-size: 12px;
    }
}

body {
    font-family: Arial, sans-serif;
    background-color: #181818;
    color: #fff;
    margin: 0;
    padding: 0;
}
.NamePage {
    font-size: 24px;
    margin: 0;
    height: 60px;
    width: 100%;
    display: flex;
    align-items: center;
    border-bottom: dotted;
    border-color: #8d8d8d;
}
nav ul {
    list-style: none;
    display: flex;
    gap: 15px;
    margin: 0;
    padding: 0;
}
nav ul li {
    color: #fff;
    cursor: pointer;
}
.sidebar {
    width: 220px;
    background-color: #20202071;
    backdrop-filter: blur(5px);
    padding: 15px;
    overflow-y: auto;
    position: fixed;
    z-index: 9;
    top: 60px;
    height: 80%;
    border: 2px solid #282828;
}
.sidebar ul {
    list-style: none;
    padding: 0;
}
.sidebar ul li {
    padding: 10px 0;
    color: #ccc;
    cursor: pointer;
}
.sidebar a {
    color: #ccc;
    text-decoration: none;
}
.content {
    width: 80%;
    padding: 20px 20px 0 280px;
}
.content h1 {
    font-size: 24px;
    margin-bottom: 15px;
}
.video-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 15px;
}
.scroll {
    display: flex;
    gap: 15px;
    overflow-x: auto;
    flex-wrap: wrap;
}